import React, { useState, useEffect } from 'react';
import { 
  Card, 
  CardContent, 
  CardMedia, 
  Typography, 
  Box,
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  Alert,
  Snackbar,
  Chip
} from '@mui/material';
import AddShoppingCartIcon from '@mui/icons-material/AddShoppingCart';
import api from '../services/api';
import { 
  cardStyles, 
  cardMediaStyles, 
  cardContentStyles,
  titleStyles, 
  categoryStyles,
  priceStyles,
  overlayStyles,
  addToCartButtonStyles,
  quantityDialogStyles,
  quantityInputStyles,
  dialogActionsStyles
} from '../styles/ProductCard.styles';

const formatPrice = (price) => {
  return new Intl.NumberFormat('vi-VN', {
    style: 'currency',
    currency: 'VND'
  }).format(price);
};

const ProductCard = ({ product, onCartUpdate }) => {
  const [openDialog, setOpenDialog] = useState(false);
  const [quantity, setQuantity] = useState(1);
  const [availableQuantity, setAvailableQuantity] = useState(product.quantity);
  const [snackbar, setSnackbar] = useState({
    open: false,
    message: '',
    severity: 'success'
  });

  useEffect(() => {
    // Ly s lng c sn t API
    const checkAvailability = async () => {
      try {
        const response = await api.get(`/products/${product.id}/availability`);
        setAvailableQuantity(response.data);
      } catch (error) {
        console.error('Error checking availability:', error);
        setAvailableQuantity(0);
      }
    };
    checkAvailability();
  }, [product.id]);

  const handleAddToCart = () => {
    if (availableQuantity > 0) {
      setOpenDialog(true);
    } else {
      setSnackbar({
        open: true,
        message: 'Product is out of stock',
        severity: 'error'
      });
    }
  };

  const handleCloseDialog = () => {
    setOpenDialog(false);
    setQuantity(1);
  };

  const handleCloseSnackbar = () => {
    setSnackbar({ ...snackbar, open: false });
  };

  const handleQuantityChange = (event) => {
    const value = parseInt(event.target.value);
    if (value > 0 && value <= availableQuantity) {
      setQuantity(value);
    }
  };

  const handleConfirmAdd = async () => {
    try {
      if (quantity > availableQuantity) {
        throw new Error('Not enough quantity available');
      }

      // Gi API  thm vo gi hng
      const response = await api.post('/cart/add', {
        productID: product.id,
        quantity: quantity
      });

      // Cp nht s lng c sn
      setAvailableQuantity(availableQuantity - quantity);

      // Callback  cp nht UI
      if (onCartUpdate) {
        onCartUpdate(response.data);
      }

      setSnackbar({
        open: true,
        message: `Added ${quantity} ${product.title} to cart`,
        severity: 'success'
      });

      handleCloseDialog();
    } catch (error) {
      console.error('Error adding to cart:', error);
      setSnackbar({
        open: true,
        message: error.response?.data?.message || 'Error adding to cart',
        severity: 'error'
      });
    }
  };

  return (
    <>
      <Card sx={cardStyles}>
        <Box sx={{ position: 'relative' }}>
          <CardMedia
            component="img"
            image={product.imageUrl || '/placeholder-book.jpg'}
            alt={product.title}
            sx={cardMediaStyles}
          />
          <Box className="overlay" sx={overlayStyles} />
          <Button
            className="add-to-cart-btn"
            variant="contained"
            startIcon={<AddShoppingCartIcon />}
            onClick={handleAddToCart}
            sx={addToCartButtonStyles}
            disabled={availableQuantity === 0}
          >
            {availableQuantity === 0 ? 'Out of Stock' : 'Add to Cart'}
          </Button>
        </Box>
        <CardContent sx={cardContentStyles}>
          <Typography 
            variant="h6" 
            component="h2"
            sx={titleStyles}
          >
            {product.title}
          </Typography>
          
          <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 1 }}>
            <Typography 
              component="span"
              sx={categoryStyles}
            >
              {product.category}
            </Typography>
            <Chip 
              label={`${availableQuantity} in stock`}
              color={availableQuantity > 0 ? "success" : "error"}
              size="small"
            />
          </Box>

          <Typography 
            variant="h6" 
            sx={priceStyles}
          >
            {formatPrice(product.currentPrice)}
          </Typography>
        </CardContent>
      </Card>

      <Dialog
        open={openDialog}
        onClose={handleCloseDialog}
        sx={quantityDialogStyles}
      >
        <DialogTitle>Add to Cart</DialogTitle>
        <DialogContent>
          <Typography variant="body1" gutterBottom>
            {product.title}
          </Typography>
          <Typography variant="body2" color="text.secondary" gutterBottom>
            Price: {formatPrice(product.currentPrice)}
          </Typography>
          <Typography variant="body2" color="text.secondary" gutterBottom>
            Available: {availableQuantity} items
          </Typography>
          <TextField
            label="Quantity"
            type="number"
            value={quantity}
            onChange={handleQuantityChange}
            inputProps={{ 
              min: 1,
              max: availableQuantity
            }}
            sx={quantityInputStyles}
            helperText={`Maximum: ${availableQuantity}`}
          />
        </DialogContent>
        <DialogActions sx={dialogActionsStyles}>
          <Button onClick={handleCloseDialog}>Cancel</Button>
          <Button 
            onClick={handleConfirmAdd} 
            variant="contained" 
            color="primary"
            disabled={quantity > availableQuantity}
          >
            Add to Cart
          </Button>
        </DialogActions>
      </Dialog>

      <Snackbar 
        open={snackbar.open} 
        autoHideDuration={3000} 
        onClose={handleCloseSnackbar}
        anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
      >
        <Alert 
          onClose={handleCloseSnackbar} 
          severity={snackbar.severity}
          variant="filled"
        >
          {snackbar.message}
        </Alert>
      </Snackbar>
    </>
  );
};

export default ProductCard; 